.pay {
  background: linear-gradient(107.56deg, #191536 0%, #000 100%);
  position: fixed;
  top: 0;
  transform: translateX(100%);
  transition: transform 0.3s ease;
  z-index: 60;
  width: 100%;
  height: 100%;

  @include iPad {
    left: 0;
    right: 0;
    margin-top: 0;
  }

  @include iPhone {
    left: 0;
    right: 0;
    margin-top: 0;
    position: fixed;
  }

  &:target {
    transform: translateX(0);
  }

  &__menu {
    grid-column: 1 / 17;
    display: grid;
    grid-template-columns: repeat(16, 1fr);
    column-gap: 20px;
    height: 250px;
    margin-top: 64px;

    @include desctop {
      grid-column: 1 / 13;
      grid-template-columns: repeat(12, 1fr);
    }

    @include iPad {
      grid-column: 1 / 7;
      grid-template-columns: repeat(6, 1fr);
      margin-top: 30px;
      height: 88px;
    }

    @include iPhone {
      grid-column: 1 / 3;
      grid-template-columns: repeat(2, 1fr);
      height: 88px;
      margin-top: 20px;
    }

    &--logo {
      display: block;
      background-image: url(../images/Logo.svg);
      background-repeat: no-repeat;
      background-size: contain;
      width: 77px;
      height: 16px;
      grid-column: 1 / 3;
      z-index: 2;
      order: 0;

      @include iPad {
        grid-column: 1 / 6;
        width: 67px;
        height: 14px;
      }

      @include iPhone {
        grid-column: 1 / 2;
        width: 58px;
        height: 12px;
      }
    }

    &--center {
      display: grid;
      grid-template-columns: repeat(10, 1fr);
      column-gap: 20px;
      grid-column: 5 / 15;

      @include desctop {
        grid-column: 4 / 12;
        grid-template-columns: repeat(8, 1fr);
      }

      @include iPad {
        grid-column: 1 / 7;
        grid-template-columns: repeat(6, 1fr);
        order: 2;
      }

      @include iPhone {
        grid-column: 1 / 3;
        grid-template-columns: repeat(6, 1fr);
        order: 2;
      }
    }

    &--item {
      font-family: "Mark-medium", sans-serif;
      font-size: 14px;
      color: #545454;
      line-height: 18px;

      @include iPhone {
        font-size: 12px;
        line-height: 16px;
      }

      &:nth-child(1) {
        color: #fff;
        grid-column: 1 / 3;

        &::after {
          content: "";
          display: none;
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background-color: #545454;
          margin-top: 10px;
          margin-left: 36px;

          @include iPad {
            width: 100%;
            height: 1px;
            margin-left: 0;
            display: block;
          }

          @include iPhone {
            width: 100%;
            height: 1px;
            margin-left: 0;
            display: block;
          }
        }

        @include desctop {
          grid-column: 1 / 3;
        }

        @include iPad {
          order: 2;
          grid-column: 1 / 3;
          display: flex;
          flex-direction: column;
          align-items: center;
        }

        @include iPhone {
          order: 2;
          grid-column: 1 / 3;
          display: flex;
          flex-direction: column;
          align-items: center;
        }
      }

      &:nth-child(2) {
        grid-column: 5 / 6;
        margin-left: -20px;
        margin-right: -20px;
        padding-left: 20px;
        color: #05c2df;

        &::after {
          content: "";
          display: block;
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background-color: #05c2df;
          margin-top: 10px;
          margin-left: 12px;

          @include iPad {
            width: 100%;
            height: 1px;
            display: block;
            margin-left: 0;
          }

          @include iPhone {
            width: 100%;
            height: 1px;
            display: block;
            margin-left: 0;
          }
        }

        @include desctop {
          grid-column: 4 / 5;
        }

        @include iPad {
          order: 2;
          grid-column: 3 / 5;
          display: flex;
          flex-direction: column;
          align-items: center;
          padding-left: 0;
        }

        @include iPhone {
          order: 2;
          grid-column: 3 / 5;
          display: flex;
          flex-direction: column;
          align-items: center;
          padding-left: 0;
        }
      }

      &:nth-child(3) {
        grid-column: 9 / 11;

        &::after {
          content: "";
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background-color: #545454;
          margin-top: 10px;
          margin-left: 36px;
          display: none;

          @include iPad {
            width: 100%;
            height: 1px;
            display: block;
            margin-top: 10px;
            margin-left: 0;
          }

          @include iPhone {
            width: 100%;
            height: 1px;
            display: block;
            margin-top: 10px;
            margin-left: 0;
          }
        }

        @include desctop {
          grid-column: 7 / 9;
        }

        @include iPad {
          order: 2;
          grid-column: 5 / 7;
          display: flex;
          flex-direction: column;
          align-items: center;
        }

        @include iPhone {
          order: 2;
          grid-column: 5 / 7;
          display: flex;
          flex-direction: column;
          align-items: center;
        }
      }

      &-order1 {
        display: none;

        @include iPad {
          display: block;
        }

        @include iPhone {
          display: block;
        }
      }

      &-order2 {
        
        @include iPad {
          display: none;
        }

        @include iPhone {
          display: none;
        }
      }
    }
  }

  &__cross {
    display: flex;
    justify-content: end;
    width: 100%;
    grid-column: 16;

    @include desctop {
      grid-column: 12;
    }

    @include iPad {
      grid-column: 6;
      order: 1;
    }

    @include iPhone {
      grid-column: 2 / 3;
      order: 1;
    }

    &--link {
      display: flex;
      background-image: url(../images/cross.svg);
      background-repeat: no-repeat;
      background-size: contain;
      width: 18px;
      height: 18px;
      margin-right: 0;
    }
  }

  &__content {
    display: grid;
    grid-column: 1 / 17;
    grid-template-columns: repeat(16, 1fr);
    column-gap: 20px;
    margin-top: -370px;

    @include desctop {
      grid-column: 1 / 13;
      grid-template-columns: repeat(12, 1fr);
    }

    @include iPad {
      grid-column: 1 / 7;
      grid-template-columns: repeat(6, 1fr);
      margin-top: 0;
    }

    @include iPhone {
      grid-column: 1 / 3;
      grid-template-columns: repeat(2, 1fr);
      margin-top: 0;
    }
  }

  &__image {
    grid-column: 1 / 9;
    height: 447px;
    position: relative;

    @include desctop {
      grid-column: 1 / 7;
      height: 279px;
    }

    @include iPad {
      grid-column: 1 / 7;
      height: 279px;
    }

    @include iPhone {
      grid-column: 1 / 3;
      height: 279px;
      display: none;
    }

    &--img {
      position: relative;
      right: 200px;
      height: 450px;
      width: 130%;

      @include desctop {
        height: 300px;
        top: 60px;
        right: 130px;
      }

      @include iPad {
        height: 280px;
        top: 0;
        right: 120px;
      }
    }
  }

  &__price {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    column-gap: 20px;
    grid-column: 1 / 9;
    order: 3;
    margin-top: -88px;

    @include desctop {
      grid-template-columns: repeat(6, 1fr);
      grid-column: 1 / 7;
      margin-top: -270px;
    }

    @include iPad {
      grid-template-columns: repeat(6, 1fr);
      grid-column: 1 / 7;
      order: 1;
      margin-top: 0;
    }

    @include iPhone {
      grid-template-columns: repeat(2, 1fr);
      grid-column: 1 / 3;
      order: 1;
      margin-top: 0;
    }

    &--1 {
      display: flex;
      flex-direction: column;
      grid-column: 1 / 3;

      @include iPad {
        grid-column: 2 / 3;
      }

      &-title {
        font-size: 16px;
        line-height: 21px;
        color: #929292;
      }

      &-how {
        z-index: 2;
      }

      &-select {
        font-size: 20px;
        line-height: 27px;
        color: #fff;
        background: #191536;
        border: none;
        outline: none;
        margin-top: 20px;
        height: 47px;
        width: 90px;

        &-item {
          border: none;
          outline: none;
          background: #191536;
        }
      }
    }

    &--2 {
      display: flex;
      flex-direction: column;
      grid-column: 3 / 5;

      @include iPad {
        grid-column: 4 / 6;
      }

      &-title {
        font-size: 16px;
        line-height: 21px;
        color: #929292;
      }

      &-price {
        font-family: "Mark-medium", sans-serif;
        font-size: 36px;
        line-height: 46px;
        margin-top: 20px;
        color: #05c2df;

        @include iPhone {
          font-size: 26px;
          line-height: 32px;
        }
      }
    }
  }

  &__input {
    grid-column: 9 / 15;
    order: 2;
    display: flex;
    flex-direction: column;

    @include desctop {
      grid-column: 7 / 13;
    }

    @include iPad {
      grid-column: 2 / 6;
    }

    @include iPhone {
      grid-column: 1 / 3;
    }
  }

  &__label {
    color: #2f2f2f;
    font-family: "Mark-regular", sans-serif;
    font-size: 14px;

    &:hover {
      color: #05c2df;
    }
  }

  &__data {
    color: #2f2f2f;
    font-family: "Mark-regular", sans-serif;
    font-size: 14px;
    display: flex;
    flex-direction: column;
    margin-right: 50px;

    &:hover {
      color: #05c2df;
    }
  }

  &__cvv {
    color: #2f2f2f;
    font-family: "Mark-regular", sans-serif;
    font-size: 14px;
    display: flex;
    flex-direction: column;

    &:hover {
      color: #05c2df;
    }
  }

  &__name {
    color: #2f2f2f;
    font-family: "Mark-regular", sans-serif;
    font-size: 14px;
    margin-top: 70px;
    line-height: 18px;

    &:hover {
      color: #05c2df;
    }
  }

  &__data-cvv {
    display: flex;
    margin-top: 50px;
  }

  &__form {
    display: flex;
    flex-direction: column;

    @include iPhone {
      display: flex;
      flex-direction: column;
    }

    &--number {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
    }
  }

  &__item {
    height: 30px;
    background-color: rgba(25, 21, 54, 0);
    border-style: none;
    border-bottom: 1px solid #2f2f2f;
    font-family: "Mark-regular", sans-serif;
    color: #fff;
    box-sizing: border-box;
    font-size: 20px;
    width: 72px;
    line-height: 130%;
    letter-spacing: 0.2em;
    outline: none;
    margin-top: 20px;

    @include iPhone {
      font-size: 18px;
      line-height: 24px;
    }

    &:hover {
      color: #fff;
      border-bottom: 1px solid #05c2df;

      .pay__label {
        color: #05c2df;
      }
    }

    &--name {
      width: 100%;
      margin-top: 0;
    }

    &--data {
      width: 94px;
    }

    &--cvv {
      width: 55px;
    }
  }

  &__visa {

    @include iPhone {
      display: none;
    }
  }

  &__button {
    width: 200px;
    height: 48px;
    border-radius: 4px;
    color: #fff;
    background-color: #05c2df;
    margin-top: 102px;
    box-sizing: border-box;
    cursor: pointer;
    grid-column: 1 / 2;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    line-height: 21px;

    @include iPad {
      width: 458px;
      grid-column: 1 / 3;
      margin-left: auto;
      margin-right: auto;
      margin-top: 58px;
    }

    @include iPhone {
      font-size: 14px;
      line-height: 18px;
    }
  }
}
